import React, {useEffect, useMemo, useState} from 'react';
import {Row, Col} from "react-grid-system";
import Section from "../../../components/section";
import Title from "../../../components/ui/title";
import {get} from "lodash";
import {useDeleteQuery, useGetOneQuery, usePostQuery} from "../../../hooks/api";
import {KEYS} from "../../../constants/key";
import {URLS} from "../../../constants/url";
import {ContentLoader, OverlayLoader} from "../../../components/loader";
import Table from "../../../components/table";
import {useStore} from "../../../store";
import {useTranslation} from "react-i18next";
import {useNavigate, useParams} from "react-router-dom";
import dayjs from "dayjs";
import Button from "../../../components/ui/button";
import Flex from "../../../components/flex";
import Form from "../../../containers/form/form";
import Field from "../../../containers/form/field";
import Modal from "../../../components/modal";
import {Trash2} from "react-feather";
import Swal from "sweetalert2";

const AgentViewContainer = ({...rest}) => {
    const {t} = useTranslation();
    const {id} = useParams()
    const navigate = useNavigate();
    const [open, setOpen] = useState(false);
    let {data, isLoading, isError} = useGetOneQuery({id, key: KEYS.agreements, url: URLS.agreements})
    const {mutate: createPolicy, isLoadingPolicy} = usePostQuery({listKeyId: KEYS.agreements})
    const {mutate: deleteRequest, isLoading: deleteLoading} = useDeleteQuery({listKeyId: KEYS.agreements})
    const setBreadcrumbs = useStore(state => get(state, 'setBreadcrumbs', () => {
    }))
    const breadcrumbs = useMemo(() => [
        {
            id: 1,
            title: t('Agreements'),
            path: '/agreements',
        },
        {
            id: 2,
            title: id,
            path: '#',
        }
    ], [data])

    useEffect(() => {
        setBreadcrumbs(breadcrumbs)
    }, [])

    const addPolicy = (data) => {
        createPolicy({url: URLS.policy, attributes: {...data, agreementsId: id, copyofdocuments: 'xxxxxxxxx'}}, {
            onSuccess: () => {
                setOpen(false);
            },
            onError: () => {

            }
        })
    }

    const remove = (id) => {
        Swal.fire({
            position: 'center',
            icon: 'error',
            backdrop: 'rgba(0,0,0,0.9)',
            background: 'none',
            title: t('Are you sure?'),
            showConfirmButton: true,
            showCancelButton: true,
            confirmButtonColor: '#d33',
            cancelButtonColor: '#13D6D1',
            confirmButtonText: t('Delete'),
            cancelButtonText:t('Cancel'),
            customClass: {
                title: 'title-color',
            },
        }).then((result) => {
            if (result.isConfirmed) {
                deleteRequest({url: `${URLS.policy}/${id}`})
            }
        });
    }

    if (isLoading || deleteLoading) {
        <OverlayLoader/>
    }

    return (
        <>
            <Section>
                <Row className={''} align={'center'}>
                    <Col xs={12}>
                        <Title>Agreement view</Title>
                    </Col>
                </Row>
                <Row>
                    <Col xs={6}>
                        <Table thead={['1', '2']}>
                            <tr>
                                <td>{t("groupofproductsId")}</td>
                                <td><strong>{get(data, "data.data.groupofproductsId.name")}</strong></td>
                            </tr>
                            <tr>
                                <td>{t("subgroupofproductsId")}</td>
                                <td><strong>{get(data, "data.data.subgroupofproductsId.name")}</strong></td>
                            </tr>
                            <tr>
                                <td>{t("products")}</td>
                                <td><strong>{get(data, "data.data.products.productname")}</strong></td>
                            </tr>
                            <tr>
                                <td>{t("startofinsurance")}</td>
                                <td>
                                    <strong>{dayjs(get(data, "data.data.startofinsurance")).format("DD/MM/YYYY")}</strong>
                                </td>
                            </tr>
                            <tr>
                                <td>{t("endofinsurance")}</td>
                                <td><strong>{dayjs(get(data, "data.data.endofinsurance")).format("DD/MM/YYYY")}</strong>
                                </td>
                            </tr>

                            <tr>
                                <td>{t("clinets")}</td>
                                <td><strong>{get(data, "data.data.clinets.forindividualsdata.name")}</strong></td>
                            </tr>


                        </Table>
                    </Col>
                    <Col xs={6}>
                        <Table thead={['1', '2']}>
                            <tr>
                                <td>{t("whoaccepted")}</td>
                                <td><strong>{get(data, "data.data.whoaccepted")}</strong></td>
                            </tr>
                            <tr>
                                <td>{t("totalinsurancepremium")}</td>
                                <td><strong>{get(data, "data.data.totalinsurancepremium")}</strong></td>
                            </tr>
                            <tr>
                                <td>{t("totalsuminsured")}</td>
                                <td><strong>{get(data, "data.data.totalsuminsured")}</strong></td>
                            </tr>
                            <tr>
                                <td>{t("paidinsurancepremium")}</td>
                                <td><strong>{get(data, "data.data.paidinsurancepremium")}</strong></td>
                            </tr>
                        </Table>
                    </Col>
                </Row>
                <Row className={'mt-15'}>
                    <Col xs={6}>
                        <Row align={'center'}>
                            <Col xs={8}><Title sm>Полис</Title></Col>
                            <Col xs={4} className={'text-right'}>
                                <Button green onClick={() => navigate(`/policy/create/${id}`)} className={'mr-16'} type={'button'}>Добавить
                                    полис</Button>

                            </Col>
                        </Row>
                        <Row className={'mt-15'}>
                            {get(data, "data.data.policy", []).length > 0 && <Col xs={12}>
                                <hr/>
                                <Table hideThead={false}
                                       thead={['Policy number', 'Form number', 'Issue date', 'Action']}>
                                    {get(data, "data.data.policy", []).map((item, i) => <tr key={i + 1}>
                                        <td>
                                            {get(item, 'policynumber', '-')}
                                        </td>
                                        <td>
                                            {get(item, 'formno', '-')}
                                        </td>
                                        <td>
                                            {dayjs(get(item, 'dateofissue')).format('DD/MM/YYYY')}
                                        </td>
                                        <td className={'cursor-pointer'}
                                            onClick={() => remove(get(item, '_id', null))}>
                                            <Trash2 color={'#dc2626'}/>
                                        </td>
                                    </tr>)}
                                </Table>
                            </Col>
                            }
                        </Row>
                    </Col>
                    <Col xs={6}>
                        <Row align={'center'}>
                            <Col xs={8}> <Title sm>Индосаменты</Title></Col>
                            <Col xs={4} className={'text-right'}> <Button onClick={() => navigate(`/policy/create/${id}`)} green type={'button'}>Добавить
                                индосаменты</Button></Col>
                        </Row>

                    </Col>
                </Row>
            </Section>
            <Modal title={'Добавить полис'} visible={open}
                   hide={setOpen}>
                {isLoadingPolicy && <ContentLoader/>}
                <Form formRequest={({data}) => {
                    addPolicy(data);
                }}
                      footer={<><Button>{t("Add")}</Button></>}>
                    <Row className={'mt-15'}>
                        <Col xs={6}>
                            <Field
                                type={'input'}
                                name={`policynumber`}
                            />
                        </Col>
                        <Col xs={6}>
                            <Field
                                type={'input'}
                                name={`formnumber`}
                            />
                        </Col>
                        <Col xs={6}>
                            <Field
                                type={'datepicker'}
                                name={`dateofissue`}
                            />
                        </Col>
                        <Col xs={6}>
                            <Field
                                type={'dropzone'}
                                name={`copyofdocuments`}
                            />
                        </Col>
                    </Row>
                </Form>
            </Modal>
        </>
    )
        ;
};

export default AgentViewContainer;